.mainContainer{
    width: 80%;
    margin: auto;
    /* border: 1px solid black; */
    height: max-content;
    margin-top: 4px;
}

.mainItemImage{
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    border: 1px solid black;
}

.backButton{
    height: 30%;
    width: 7%;
    margin-left: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image{
    height: 100%;
    width: 65%;
}

.forwardButton{
    height: 30%;
    width: 7%;
    margin-right: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageCollection{
    border: 1px solid black;
    margin-top: 3px;
    height: 100px;
    display: flex;
}

.sellerCard{
    border: 1px solid black;
    margin-top: 3px;
    height: 120px;
    display: flex;
}

.sellerImage{
    height: 95%;
    float: left;
}

.priceAndOverview{
    border: 1px solid black;
    margin-top: 3px;
    display: flex;
    height: max-content;
}

.priceAndAddress{
    width: 50%;
    border: 1px solid black;
}

.overviewTextArea{
    width: 97%;
    margin-left: 4px;
    resize: none;
    font-family: Arial, Helvetica, sans-serif;
    /* border: 1px solid black; */
    outline: 1px black;
    border-radius: 8px;
    overflow: hidden;
    box-sizing: border-box;
}

.overView{
    width: 50%;
    border: 1px solid black;
}

.productDetial{
    border: 1px solid black;
    height: max-content;
    margin-top: 3px;
}

.textDetails{
    width: 99%;
    margin-left: 4px;
    resize: none;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid black;
    outline: 1px black;
    border-radius: 8px;
    box-sizing: border-box;
    overflow: hidden;
}


@media screen and (min-width: 320px) and (max-width: 576px) {
    .mainContainer{
        width: 98%;
    }

    .mainItemImage{
        height: 220px;
    }

    .imageCollection{
        border: 1px solid black;
        margin-top: 1px;
        margin-left: 0;
        height: 100px;
        display: flex;
    }
}