.loginPage{
    /* position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); */
    width: 35%;
    border: 2px solid black;
    box-sizing: border-box;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 12px;
    padding-bottom: 12px;
    border-radius: 8px;
    border-color: black;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
}


.emailLabel{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    font-weight: 500;
}

.inputEmail{
    padding-top: 0px;
    padding-left: 10px;
    margin-top: 5px;
    font-size: large;
    width: 90%;
    height: 43px;
    box-sizing: border-box;
    outline-width: 1px;
    outline-color: lightblue;
    border-radius: 8px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-bottom: 18px;
}

.passwordLabel{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    font-weight: 500;
}

.loginButton{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 90%;
    font-size: 22px;
    border-radius: 8px;
    height: 40px;
    padding-bottom: 5px;
    margin-top: 10px;
}

.mousepointer{
    cursor: pointer;
    color: dimgray;
}

.mousepointer:hover{
    text-decoration: underline;
}


@media screen and (min-width: 320px) and (max-width: 576px) {
    .loginPage{
        width: 98%;
        margin-top: 30px;
    }

    .inputEmail{
        width: 100%;
    }
}